import { Component } from '@angular/core';
import { HttpService } from './helper/http.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  url = 'https://medium.com/m/signin?redirect=https%3A%2F%2Fmedium.com%2Fm%2Foauth%2Fauthorize%3Fclient_id%3D9964c11e229b%26scope%3DbasicProfile%252CpublishPost%26response_type%3Dcode%26redirect_uri%3Dlocalhso&loginType=default'
  constructor(public api: HttpService) {
    const client_id = '9964c11e229b'; // 67f48d382b16cffbbe67d71e49d9c9f5ba325a5e
    // const scope = 'basicProfile';
    // const state = '';
    // const response_type = '';
    // const redirect_uri	= '';
    this.api.medium('/m/oauth/authorize', ' '); // ?client_id=' + client_id + '&scope=listPublications&redirect_uri=' + 'localhost:4200'
  }
}



